*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

a {
  display: inline-block;
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  background-color: none;
}

input {
  display: inline-block;
  border: none;
}

img {
  display: inline-block;
  max-width: 100%;
}

/*
  FONT SIZES
  75px | 56px | 42px | 32px | 24px | 17px | 13px | 10px | 7.5px | 5.6px | 4.2px
*/
html {
  font-size: 50%;
}
@media (min-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 62.5%;
  }
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: #6E7A89;
}

.heading-primary {
  font-size: 4.2rem;
  color: #FFFFFF;
}
@media (min-width: 56.25em) {
  .heading-primary {
    width: 85%;
    font-size: 5.6rem;
  }
}
@media (min-width: 75em) {
  .heading-primary {
    line-height: 1.5;
    font-size: 6.2rem;
  }
}

.heading-secondary {
  margin: 0 auto;
  margin-bottom: 1.6rem;
  width: 85%;
  font-size: 4rem;
  font-weight: 600;
  color: #132742;
  text-align: center;
  line-height: 1.2;
}
@media (min-width: 56.25em) {
  .heading-secondary {
    text-align: left;
    line-height: 1.5;
    width: 100%;
  }
}

.heading-tertiary {
  font-size: 2.4rem;
  color: #132742;
  margin-bottom: 1.2rem;
}

.button {
  padding: 1.4rem 2.4rem;
  border-radius: 10px;
  font-size: 1.6rem;
  font-weight: 600;
  font-family: inherit;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease-out;
}

.button--outlined {
  background-color: transparent;
  color: #FFFFFF;
  font-weight: 400;
  border: 0.1px solid #FFFFFF;
}
.button--outlined:hover {
  font-weight: 600;
  background-color: #FFFFFF;
  color: #1A2222;
}

.button--white {
  background-color: #FFFFFF;
  border: 1px solid transparent;
}
.button--white:hover {
  background-color: #1A2222;
  color: #FFFFFF;
  border: 1px solid currentColor;
}

.button--orange {
  background-color: #CD412E;
  color: #FFFFFF;
  border: 1px solid transparent;
}
.button--orange:hover {
  background-color: transparent;
  border: 1px solid #CD412E;
  color: #CD412E;
}

.card {
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
.card img {
  width: 80%;
  border-radius: 10px;
}
@media (min-width: 75em) {
  .card img {
    width: 100%;
  }
}

.header-section {
  margin-bottom: 4rem;
}
.header-section__text {
  text-align: center;
}
@media (min-width: 75em) {
  .header-section__text {
    text-align: left;
  }
}

.link:link, .link:visited {
  color: #FFFFFF;
  padding: 1rem;
  border-bottom: 1px solid transparent;
  transition: all 0.4s ease-out;
  width: 15rem;
  font-size: 1.8rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
@media (min-width: 112.5em) {
  .link:link, .link:visited {
    text-align: left;
  }
}
.link:hover, .link:active {
  border-bottom: 1px solid currentColor;
}
.link--white:link, .link--white:visited {
  display: unset;
  color: #FFFFFF;
}
@media (min-width: 112.5em) {
  .link--white:link, .link--white:visited {
    color: #000000;
  }
}
.link--black:link, .link--black:visited {
  color: #000000;
}

.container {
  margin: 0 auto;
  width: 130rem;
  max-width: 95%;
}

.grid {
  display: grid;
  place-items: center;
}
.grid-2-cols {
  grid-template-columns: repeat(2, 1fr);
}
.grid-3-cols {
  grid-template-columns: repeat(3, 1fr);
}
.grid-4-cols {
  grid-template-columns: repeat(4, 1fr);
}
.grid-5-cols {
  grid-template-columns: repeat(5, 1fr);
}
.grid-3-gap {
  gap: 3rem;
}
.grid-1to2 {
  grid-template-columns: 1fr;
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-1to2 {
    grid-template-columns: repeat(2, 1fr);
    gap: 5.6rem;
  }
}
.grid-1to3 {
  grid-template-columns: 1fr;
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-1to3 {
    grid-template-columns: repeat(3, 1fr);
    gap: 4.8rem;
  }
}
.grid-2to3 {
  grid-template-columns: repeat(2, 1fr);
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-2to3 {
    grid-template-columns: repeat(3, 1fr);
    gap: 4.8rem;
  }
}
.grid-2to7 {
  grid-template-columns: repeat(2, 1fr);
  gap: 3.2rem;
}
@media (min-width: 56.25em) {
  .grid-2to7 {
    grid-template-columns: repeat(7, 1fr);
    gap: 1.2rem;
  }
}

.header {
  background-color: #1A2222;
  height: 7rem;
}
.header-fixed {
  width: 100%;
  position: fixed;
  z-index: 10;
  box-shadow: 3px 5px 5px rgba(0, 0, 0, 0.15);
}
@media (min-width: 56.25em) {
  .header {
    height: 8rem;
  }
}
.header__title {
  font-size: 2.4rem;
  font-weight: 600;
  color: #FFFFFF;
}
.header__container {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
@media (min-width: 56.25em) {
  .header__container {
    gap: 4rem;
  }
}
.header__button {
  display: none;
}
@media (min-width: 56.25em) {
  .header__button {
    display: inline-block;
  }
}

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  transform: translateX(100%);
  transition: all 0.4s ease-out;
  background-color: #1A2222;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 56.25em) {
  .navigation {
    position: unset;
    transform: translateX(0%);
    flex-direction: row;
    align-items: center;
  }
}
.navigation__information {
  font-size: 1.2rem;
}
@media (min-width: 56.25em) {
  .navigation__information {
    display: none;
  }
}
.navigation__list {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 56.25em) {
  .navigation__list {
    flex-direction: row;
    gap: 6.4rem;
  }
}
.navigation__link {
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 600;
  margin-bottom: 5.6rem;
  border-bottom: 1px solid transparent;
  transition: all 0.4s ease-out;
}
.navigation__link:hover {
  border-bottom-color: #CD412E;
}
@media (min-width: 56.25em) {
  .navigation__link {
    font-size: 1.6rem;
    font-weight: 400;
    margin-bottom: 0;
  }
}
.navigation__open {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
.navigation__close {
  width: 100%;
  padding-right: 2.4rem;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: flex-end;
}
.navigation__open, .navigation__close {
  font-size: 4.8rem;
  color: #FFFFFF;
}
.navigation__open-icon, .navigation__close-icon {
  transition: all 0.4s ease-out;
  cursor: pointer;
}
.navigation__open-icon:hover, .navigation__close-icon:hover {
  color: #CD412E;
}
@media (min-width: 56.25em) {
  .navigation__open, .navigation__close {
    display: none;
  }
}

.show.navigation {
  transform: translateX(0%);
}

.footer {
  padding: 7rem 0;
}
@media (min-width: 75em) {
  .footer {
    padding: 9.6rem 0;
  }
}
.footer__content {
  grid-column: 1/3;
}
.footer__title {
  font-size: 3rem;
  color: #000000;
}
.footer__list {
  display: flex;
  align-items: center;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.6rem;
}
.footer__menu, .footer__content {
  margin-bottom: auto;
}
@media (min-width: 75em) {
  .footer__menu:nth-child(4), .footer__content:nth-child(4) {
    grid-column: 5/7;
  }
}
.footer__item a:link, .footer__item a:visited {
  transition: all 0.4s ease-out;
  color: #6E7A89;
}
.footer__item a:hover, .footer__item a:active {
  color: #1A2222;
}
.footer__item-main {
  font-size: 1.8rem;
  font-weight: 600;
  color: #000000;
}
.footer__icon {
  color: #1A2222;
}

.hero {
  padding: 7rem 0;
  background-color: #1A2222;
  position: relative;
}
@media (min-width: 75em) {
  .hero {
    height: 75rem;
    margin-bottom: 38.4rem;
  }
}
.hero__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 3.2rem;
}
.hero__description {
  width: 80%;
  color: #FFFFFF;
  font-size: 1.8rem;
}
@media (min-width: 56.25em) {
  .hero__description {
    width: 60%;
  }
}
@media (min-width: 75em) {
  .hero__description {
    width: 40%;
  }
}
.hero__imagesBox {
  margin-bottom: 2rem;
}
@media (min-width: 112.5em) {
  .hero__imagesBox {
    margin-bottom: 9.6rem;
  }
}
.hero__image {
  margin: 0 auto;
}
.hero__image-box {
  width: 70%;
  overflow: hidden;
}
@media (min-width: 75em) {
  .hero__image-box {
    width: 100%;
  }
}
.hero__image-box img {
  border-radius: 9px;
}
.hero__image-box--1 {
  grid-column: 1/6;
}
@media (min-width: 75em) {
  .hero__image-box--1 {
    align-self: start;
    grid-column: 1/3;
  }
}
.hero__image-box--2 {
  display: none;
}
@media (min-width: 75em) {
  .hero__image-box--2 {
    display: unset;
    grid-column: 3/6;
  }
}
.hero__image-text {
  color: #FFFFFF;
  margin: 1.6rem 0;
}
@media (min-width: 112.5em) {
  .hero__image-text {
    width: 60%;
    text-align: left;
    font-size: 1.6rem;
    color: #6E7A89;
  }
}
.hero__item {
  position: absolute;
  width: 25rem;
}
@media (min-width: 56.25em) {
  .hero__item {
    width: 45rem;
  }
}
.hero__item--orange {
  top: 0;
  right: 0;
}
@media (min-width: 56.25em) {
  .hero__item--orange {
    width: 30rem;
    top: 5%;
    left: 0%;
  }
}
@media (min-width: 75em) {
  .hero__item--orange {
    top: 0%;
  }
}
@media (min-width: 112.5em) {
  .hero__item--orange {
    left: 20%;
  }
}
.hero__item--blue {
  top: 0;
  left: 0;
  z-index: -1;
}
@media (min-width: 56.25em) {
  .hero__item--blue {
    top: 50%;
  }
}
@media (min-width: 75em) {
  .hero__item--blue {
    top: 35%;
    z-index: unset;
  }
}
@media (min-width: 112.5em) {
  .hero__item--blue {
    top: 25%;
  }
}
.hero__item--purple {
  bottom: -10%;
  right: -3%;
}
@media (min-width: 56.25em) {
  .hero__item--purple {
    top: 5%;
  }
}

.features {
  padding: 9.6rem 0;
}
.features__content {
  padding: 3.2rem;
}
.features__list {
  margin: 0 auto;
  margin-bottom: 4rem;
  padding-left: 4rem;
}
@media (min-width: 75em) {
  .features__list {
    margin-bottom: 8rem;
  }
}
.features__item {
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}
.features__item p {
  font-weight: 700;
  color: #132742;
}
.features__icon {
  color: #CD412E;
  font-weight: 700;
  font-size: 2.4rem;
}
.features__button {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: flex-end;
}
@media (min-width: 56.25em) {
  .features__button {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: flex-start;
  }
}
.features__visual {
  position: relative;
}
.features__heart {
  position: absolute;
  background-color: #FFFFFF;
  border-radius: 10px;
  padding: 1.5rem 3rem;
  top: 5%;
  left: 5%;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
}
@media (min-width: 75em) {
  .features__heart {
    top: 65%;
    left: -20%;
  }
}
.features__heart-icon {
  font-size: 3.2rem;
  font-weight: 700;
  color: #CD412E;
}
.features__heart-number {
  font-size: 2.4rem;
  font-weight: 600;
  color: #CD412E;
}
.features__heart-number span {
  color: #6E7A89;
  font-weight: 400;
  font-size: 1.6rem;
}
.features__image {
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
.features__image img {
  width: 80%;
  border-radius: 10px;
}
@media (min-width: 75em) {
  .features__image img {
    width: 100%;
  }
}

.about {
  padding: 9.6rem 0;
}
.about__content {
  margin-bottom: 6rem;
  padding: 3.2rem;
}
@media (min-width: 56.25em) {
  .about__content {
    grid-row: 1/2;
    grid-column: 2/3;
  }
}
.about__numberBox {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.about__number {
  font-size: 3.2rem;
  font-weight: 600;
  color: #1A2222;
}
@media (min-width: 75em) {
  .about__number {
    font-size: 4.8rem;
  }
}
.about__visual {
  position: relative;
}
.about__overlay {
  width: 80%;
}
.about__overlay img {
  width: 100%;
}
.about__image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
@media (min-width: 56.25em) {
  .about__image {
    top: 58%;
  }
}
.about__image img {
  width: 100%;
}

.classes {
  padding: 7rem 0;
  background-color: #FEDEFD;
}
@media (min-width: 75em) {
  .classes {
    padding: 9.6rem 0;
  }
}
.classes__header {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 6rem;
}
@media (min-width: 75em) {
  .classes__header {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }
}
.classes__card-image {
  position: relative;
}
.classes__card-image picture {
  overflow: hidden;
}
.classes__card-image img {
  transition: all 0.4s ease-out;
  border-radius: 10px;
  width: 100%;
}
.classes__card-image img:hover {
  transform: scale(1.01);
}
.classes__card-icon {
  position: absolute;
  color: #FFFFFF;
  font-size: 3rem;
  bottom: 10%;
  left: 10%;
}
.classes__card-footer {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.classes__card-user {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  gap: 1rem;
}
.classes__card-name {
  color: #000000;
}
.classes__card-iconBox {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  gap: 1rem;
}
.classes__card-iconTime {
  font-size: 2.4rem;
  color: #CD412E;
}
.classes__high {
  position: relative;
}
.classes__high-icon {
  position: absolute;
  top: 10%;
  right: 10%;
  color: #FFFFFF;
  font-size: 3rem;
}
.classes__high-image picture {
  overflow: hidden;
}
.classes__high-image img {
  transition: all 0.4s ease-out;
  border-radius: 10px;
  width: 100%;
}
.classes__high-image img:hover {
  transform: scale(1.01);
}
.classes__high-content {
  position: absolute;
  bottom: 5%;
  left: 5%;
  color: #FFFFFF;
  width: 80%;
}
.classes__high-content .heading-tertiary {
  color: #FFFFFF;
}
.classes__high-footer {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.classes__high-user {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  gap: 1rem;
}
.classes__high-name {
  color: #FFFFFF;
}
.classes__high-iconBox {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  gap: 1rem;
}
.classes__high-iconTime {
  font-size: 2.4rem;
  color: #FFFFFF;
}

.testimonials {
  padding: 7rem 0;
}
@media (min-width: 75em) {
  .testimonials {
    padding: 12rem 0;
  }
}
.testimonials__content {
  padding: 2rem;
  margin-bottom: 6rem;
}
.testimonials__visual {
  position: relative;
}
.testimonials__image {
  position: absolute;
  top: 46%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.testimonials__name {
  font-weight: 700;
  font-size: 2rem;
  color: #132742;
}

.trainers {
  padding: 7rem 0;
}
@media (min-width: 75em) {
  .trainers {
    padding: 12rem 0;
  }
}
.trainers__title {
  margin-bottom: 6rem;
}
@media (min-width: 56.25em) {
  .trainers__title {
    width: 40%;
  }
}
.trainers__image {
  position: relative;
  border-radius: 10px;
  transition: all 0.4s ease-out;
}
.trainers__image .card img {
  width: 100%;
}
.trainers__image-content {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
  color: #FFFFFF;
  opacity: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: all 0.4s ease-out;
}
.trainers__image-name {
  font-weight: 700;
  font-size: 2.4rem;
}
.trainers__image::after {
  position: absolute;
  content: "";
  border-radius: 10px;
  display: inline-block;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0;
  transition: all 0.4s ease-out;
}
.trainers__image:hover::after {
  opacity: 1;
}
.trainers__image:hover .trainers__image-content {
  opacity: 1;
}

.newsletter {
  padding: 7rem 0;
}
@media (min-width: 75em) {
  .newsletter {
    padding: 12rem 0;
  }
}
.newsletter__container {
  position: relative;
  border-radius: 30px;
  padding: 3.2rem;
  background-color: #1A2222;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 75em) {
  .newsletter__container {
    padding: 10rem 3.2rem;
  }
}
.newsletter__title {
  width: 70%;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 75em) {
  .newsletter__title {
    width: 100%;
  }
}
.newsletter__text {
  width: 80%;
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 4rem;
}
.newsletter__form {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
}
@media (min-width: 56.25em) {
  .newsletter__form {
    display: grid;
    grid-template-columns: 80% 20%;
  }
}
@media (min-width: 75em) {
  .newsletter__form {
    width: 60%;
  }
}
.newsletter__form-input {
  font-family: inherit;
  padding: 1.5rem;
  width: 100%;
  border-radius: 9px;
  height: 5rem;
}
.newsletter__image {
  position: absolute;
  width: 15rem;
}
@media (min-width: 75em) {
  .newsletter__image {
    width: 30rem;
  }
}
